import * as React from 'react';
import Snackbar from '@mui/material/Snackbar';
import Paper from '@mui/material/Paper';
import InputBase from '@mui/material/InputBase';
import IconButton from '@mui/material/IconButton';
import UploadIcon from '@mui/icons-material/Upload';
import FormControl from '@mui/material/FormControl';

export default function Main({onClick}){
  const { vertical, horizontal, open } = {open:true,vertical:"bottom",horizontal:"center"};
  const inputRef = React.useRef();
  
  const ClearTextBox = () => {
    inputRef.current.value = ""
  }

  const AddItemAndTextReset = (e) =>{
    onClick(e)
    ClearTextBox()
  }

  const AppendTextComponent = (
    <FormControl onSubmit = {AddItemAndTextReset}>
      <Paper
        component="form"
        sx={{ p: '2px 4px', display: 'flex', alignItems: 'center', width: 400 }}
      >
          <InputBase
            name = "memoTextInput"
            sx={{ ml: 1, flex: 1}}
            placeholder=""
            inputProps={{ 'aria-label': 'memoTextInput' }}
            inputRef = {inputRef}
          />
          <IconButton type="submit" sx={{ p: '10px' }} aria-label="append">
            <UploadIcon color = "primary"/>
          </IconButton>
      </Paper>
    </FormControl>
  );

  return (
    <div>
      <Snackbar
      anchorOrigin={{ vertical, horizontal }}
      open={open}
      key={vertical + horizontal}
      >
      {AppendTextComponent}
    </Snackbar>
    </div>
);
} 
