import * as React from 'react';
import AddMemoBar from "./AddMemoBar"
import MemoList from "./MemoList"

import axios from "axios"


export default function Main(){
  const [memoList,setMemoList] = React.useState(["Loading..."]);
  const apiUrl = "https://script.google.com/a/macros/g.kobe-kosen.ac.jp/s/AKfycbzd8HOiMEzNPchb7eXDxHkMZb4Z033NQoizsLLKJVkAi3VGJeQaqS_JUy-hkjFLJs4-/exec"
  
  const GetListData = () =>{
    React.useEffect(() => {
      axios
      .get(apiUrl)             //path
      .then((response) => {
        setMemoList(response.data.data.item);
      })                               //poststhen
      .catch(() => {
        console.log('');
      });                             //(catch)
  }, [])
  }

  const addItem = (itemAdd) => {
    if (!memoList.includes(itemAdd) && itemAdd) {
      setMemoList((prev) => [...prev,itemAdd]);
    }
  };

  const handleRemoveItem = (itemRemove) =>{
    setMemoList((prev) => [...prev.filter((i) => i !== itemRemove)]);
  }

  const handleAddButtonClick = (e) => {
    // 
    e.preventDefault();

    // post
    const form = e.target;
    const formData = new FormData(form);
    const formJson = Object.fromEntries(formData.entries());
    addItem(formJson["memoTextInput"])
  }

  GetListData();
  return(
    <>
      <MemoList itemList = {memoList} onRemoveItemClick = {handleRemoveItem}/>
      <AddMemoBar  onClick = {handleAddButtonClick}/>
    </>
  );
}